.mobileNav {
    background-color: #fff;
    position: absolute;
    width: 100%;
    z-index: 3;
}
a {
    color: #000;
}
.mobileNav ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
    background-color: #fff;
}

.mobileNav li a {
    display: block;
    padding: 20px;
    /* border-right: 0px solid #f4f4f4; */
    text-decoration: none;
    font-size: 20px;
}

.mobileNav li a:hover,
.mobileNav .menu-btn:hover {
    background-color: rgb(206, 214, 213);
    color: #000;

}
/* menu */

.mobileNav .menu {
    clear: both;
    max-height: 0;
    transition: max-height 0.2s ease-out;
    margin-top: 2.5px;
    margin-bottom: 2.5px;
}

/* menu icon */

.mobileNav .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 28px 20px;
    position: relative;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    top: 2px;
}

.mobileNav .menu-icon .navicon {
    background: #333;
    display: block;
    height: 2px;
    position: relative;
    transition: 0.2s ease-out;
    width: 18px;
}

.mobileNav .menu-icon .navicon:before,
.mobileNav .menu-icon .navicon:after {
    background: #333;
    content: "";
    display: block;
    height: 100%;
    position: absolute;
    transition: all 0.4s ease-out;
    width: 100%;
}

.mobileNav .menu-icon .navicon:before {
    top: 5px;
}

.mobileNav .menu-icon .navicon:after {
    top: -5px;
}

/* menu btn */

.mobileNav .menu-btn {
    display: none;
}

.mobileNav .menu-btn:checked ~ .menu {
    max-height: 100%;
}

.mobileNav .menu-btn:checked ~ .menu-icon .navicon {
    background: transparent;
}

.mobileNav .menu-btn:checked ~ .menu-icon .navicon:before {
    transform: rotate(-45deg);
}

.mobileNav .menu-btn:checked ~ .menu-icon .navicon:after {
    transform: rotate(45deg);
}

.mobileNav .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.mobileNav .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
    top: 0;
}
/* 48em = 768px */
@media(min-width: 48em) {
    .mobileNav {
        position: relative;
    }
    .mobileNav li {
        float: left;
    }
    .mobileNav li a {
        padding: 8px 17px;
    }
    .mobileNav .menu {
        clear: none;
        float: right;
        max-height: 100% !important;
    }
    .mobileNav .menu-icon {
        display: none;
    }
}
@media(max-width: 867px) {
    .mobileNav li a {
        font-size: 14px;
